.home {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap: 20px;
}

.home .article {
    margin-bottom: 3rem;
    padding: 2rem 2rem;
    border: 1px solid #cfcfcf;
    border-radius: 4px;
    background: #a58b64;
}

.home .article:hover {
    box-shadow: 3px 3px 4px #000000;
}

.home .article img{
    width: 100%;
}

#home-content-data{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 50px;
    
}

#home-h1 {
    font-size: 3rem;
    background: #5b0000;
    color: aliceblue;
    display: grid;
    padding: 0.3em 0.5em;
    border-radius: .5rem;
    width: 18%;
    box-shadow: 1px 1px 2px rgb(186, 181, 181), 0 0 25px rgb(131, 131, 131), 0 0 5px rgb(192, 192, 192);
    justify-items: center;
}
